/* eslint-disable camelcase */
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CustomInput,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import IntlMessages from '../../../../helpers/IntlMessages';
import { adminRoot } from '../../../../constants/defaultValues';
import instance from '../../../../helpers/axiosInstance';
import {  setProfileData } from '../../../../helpers/Utils';



const WorkExperience = ({
  editWorkExperience,
  toggleEditWorkExperence,
  profileData,
}) => {
  console.log(profileData);
  const { user_id,id,employer,role,joining_data,leaving_data,job_description } = profileData;
  const history = useHistory();
  const [noExperience, setNoExperience] = useState(false);

  const [formValues, setFormValues] = useState({
    employer,
    role,
    joining_data,
    leaving_data,
    job_description,
  });

  const handleChange = (event, key) => {
    setFormValues({
      ...formValues,
      [key]: event.target.value,
    });
  };

  const handleSuccessButtonClick = (newData) => {
    return new Promise((resolve, reject) => {
      const updateProfile = async () => {
        const response = await instance({
          method: 'POST',
          url: '/updateProfile',
          data: { user_id,id, ...newData },
        });

        const { data } = response;

        if (data.success) {
          setProfileData({ ...profileData, ...newData });
          resolve('Profile updated successfully!');
          history.push(`${adminRoot}/profile/links`);
        } else {
          reject('Failed to update profile!');
        }
      };

      updateProfile();
    });
  };  

  return (
    <Card className="mb-4">
      <div className="position-absolute card-top-buttons">
        <Button
          outline
          color="primary"
          onClick={() => {
            toggleEditWorkExperence(!editWorkExperience);
          }}
          className="icon-button"
        >
          <i className="simple-icon-pencil" />
        </Button>
      </div>
      <CardBody>
        <CardTitle>
          <h2 className="lead">
            <IntlMessages id="forms.work-experience" />
          </h2>
        </CardTitle>

        <Separator className="mb-3" />

        <FormGroup>
          <CustomInput
            type="checkbox"
            id="formal-education"
            disabled={!editWorkExperience}
            label="I am yet to find opportunity."
            checked={noExperience}
            onChange={() => setNoExperience(!noExperience)}
          />
        </FormGroup>

        <Row>
          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.employer" />
              </Label>
              <Input
                type="text"
                name="employer"
                placeholder="employer"
                value={formValues.employer}
                onChange={(event) => {
                  handleChange(event, 'employer');
                }}
                disabled={!editWorkExperience || noExperience}
              />
            </FormGroup>
          </Colxx>
          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.role" />
              </Label>
              <Input
                type="text"
                name="role"
                placeholder="Role"
                value={formValues.role}
                onChange={(event) => {
                  handleChange(event, 'role');
                }}
                disabled={!editWorkExperience || noExperience}
              />
            </FormGroup>
          </Colxx>
        </Row>

        <Row>
          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.from" />
              </Label>
              <Input
                type="text"
                name="joining_data"
                placeholder="2021"
                value={formValues.joining_data}
                onChange={(event) => {
                  handleChange(event, 'joining_data');
                }}
                disabled={!editWorkExperience || noExperience}
              />
            </FormGroup>
          </Colxx>

          <Colxx md="6">
            <FormGroup className="mb-3">
              <Label>
                <IntlMessages id="forms.to" />
              </Label>
              <Input
                type="text"
                name="leaving_data"
                placeholder="04"
                value={formValues.leaving_data}
                onChange={(event) => {
                  handleChange(event, 'leaving_data');
                }}
                disabled={!editWorkExperience || noExperience}
              />
            </FormGroup>
          </Colxx>
        </Row>

        <FormGroup className="mb-3">
          <Label>
            <IntlMessages id="forms.description" />
          </Label>
          <Input
            type="textarea"
            name="job_description"
            placeholder="Desribe your work experience"
                value={formValues.job_description}
                onChange={(event) => {
                  handleChange(event, 'job_description');
                }}
            rows="6"
            disabled={!editWorkExperience || noExperience}
          />
        </FormGroup>

        {editWorkExperience && (
          <Colxx xxs="12" className="text-center">
            <StateButton
              id="emergencySubmitButton"
              color="primary"
              onClick={()=>handleSuccessButtonClick(formValues)}
              next={() => {
                toggleEditWorkExperence(false);
              }}
            >
              <IntlMessages id="forms.submit" />
            </StateButton>
          </Colxx>
        )}
      </CardBody>
    </Card>
  );
};

export default WorkExperience;
